import { component } from 'picoapp';

const selectors = {
  variantSelectors: '[name*=options]',
  productForm: '#AddToCartForm',
  addToCartButton: '[data-add-to-cart]'
};

class MobileProductForm {
  constructor(el, ctx) {
    this.el = el;
    this.ctx = ctx;
    this.init();
    this.bindEvents();
  }

  init() {
    // Cache DOM nodes
    this.productForm = document.querySelector(selectors.productForm);
    this.variantSelectors = this.el.querySelectorAll(selectors.variantSelectors);
    this.addToCartButton = this.el.querySelector(selectors.addToCartButton);
  }

  bindEvents() {
    this.variantSelectors.forEach(select => {
      select.addEventListener('change', () => {
        this.productForm.querySelector(`#${select.id}`).value = select.value
        this.productForm.querySelector(`#${select.id}`).dispatchEvent(new Event('change'));
      })
    })
    this.addToCartButton.addEventListener('click', () => {
      this.productForm.querySelector(selectors.addToCartButton).click();
    })
  }
}

export default component((node, ctx) => {
  new MobileProductForm(node, ctx);
});
